export default (state = initialState, action) => {
  switch (action.type) {
    case ADD_DIALOGBOX:
      let newDialogBoxInfo = state.dialogBoxInfo;
      newDialogBoxInfo.push({
        id: action.payload.id,
        image: action.payload.image,
        content: "",
        diagPosition: [0, 0],
        isChoice: action.payload.isChoice
      });
      return {
        ...state,
        dialogBoxInfo: newDialogBoxInfo,
        howManyDialogBoxes: newDialogBoxInfo.length
      };
    case REMOVE_DIALOGBOX:
      return {
        ...state,
        dialogBoxInfo: action.payload,
        howManyDialogBoxes: action.payload.length
      };
    case UPDATE_DIALOGBOX_TEXT:
      return {
        ...state,
        dialogBoxInfo: action.payload,
        howManyDialogBoxes: action.payload.length
      };
    case UPDATE_DROPPING_ID:
      return {
        ...state,
        droppingId: action.payload
      };
    case UPDATE_DIAG_POSITION:
      return {
        ...state,
        dialogBoxInfo: action.payload,
        howManyDialogBoxes: action.payload.length
      };
    case UPDATE_STATE:
      if (action.payload === "reset") {
        return {
          ...state,
          stateChanged: 0
        };
      } else {
        return {
          ...state,
          stateChanged: state.stateChanged + action.payload
        };
      }
    case CREATE_NODE_CONNECTION:
      return {
        ...state,
        nodeConnections: action.payload
      };
    case SORT_DIALOG_SEQUENCE:
      return {
        ...state,
        sortedDialogSequence: action.payload
      };
    default:
      return state;
  }
};
