import React, { Component } from "react";
import TopBar from "./components/TopBar";
import DialogEditor from "./components/DialogEditor";
import DialogTester from "./components/DialogTester";
import { Provider } from "react-redux";
import store from "./store";

class App extends Component {
  constructor(props) {
    super(props);
    this.toggleShowTester = this.toggleShowTester.bind(this);
    this.toggleHideEditor = this.toggleHideEditor.bind(this);
    this.state = {
      showTester: false,
      hideEditor: false
    };
  }

  toggleShowTester() {
    this.setState({ showTester: !this.state.showTester });
  }

  toggleHideEditor() {
    this.setState({ hideEditor: !this.state.hideEditor });
  }

  render() {
    return (
      <Provider store={store}>
        <TopBar
          toggleTest={this.toggleShowTester}
          editorState={this.state.hideEditor}
          toggleHideEditor={this.toggleHideEditor}
        />
        {!this.state.hideEditor && <DialogEditor />}
        {this.state.showTester && (
          <DialogTester toggleHideEditor={this.toggleHideEditor} />
        )}
      </Provider>
    );
  }
}

export default App;
