import React, { Component } from "react";
import "../styles/style.css";
import { addDialogBox } from "../actions/dialogActions";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Bean, Elfo, Luci, Mertz, Zog } from "./images/Images";
import { animateScroll } from "react-scroll";

class TopBar extends Component {
  constructor(props) {
    super(props);
    this.handleAddDialogBox = this.handleAddDialogBox.bind(this);
    this.handleToggleShowChar = this.handleToggleShowChar.bind(this);
    this.handleToggleShowCharChoice = this.handleToggleShowCharChoice.bind(
      this
    );
    this.playButtonPressed = this.playButtonPressed.bind(this);
    this.state = {
      toggleShowChar: false,
      toggleShowCharChoice: false
    };
  }
  handleAddDialogBox(charName, isChoice) {
    this.props.addDialogBox(charName, this.props.dialogBoxInfo, isChoice);
    isChoice ? this.handleToggleShowCharChoice() : this.handleToggleShowChar();
  }

  handleToggleShowChar() {
    this.setState({ toggleShowChar: !this.state.toggleShowChar });
  }

  handleToggleShowCharChoice() {
    this.setState({ toggleShowCharChoice: !this.state.toggleShowCharChoice });
  }

  playButtonPressed() {
    this.scrollTop();
    this.props.toggleTest();
    if (this.props.editorState) {
      this.props.toggleHideEditor();
    }
  }
  scrollTop() {
    animateScroll.scrollToTop();
  }

  render() {
    return (
      <div className="topbar">
        <div className="title" title="DIALOG CREATOR">
          DIALOG CREATOR
        </div>
        <div className="tools">
          <button
            onClick={this.handleToggleShowChar}
            className="new-char-button"
          >
            <p>New Character</p>
          </button>
          <button
            onClick={this.handleToggleShowCharChoice}
            className="new-char-button"
          >
            <p>New Choice</p>
          </button>
          <div className="tools-middle">
            <button className="play-button" onClick={this.playButtonPressed}>
              <div className="play-icon"></div>
            </button>
          </div>

          {this.state.toggleShowChar && (
            ///THIS IS TEMPORARY
            <div className="choose-next-character">
              <button onClick={() => this.handleAddDialogBox("Bean", false)}>
                <img src={Bean} alt="Bean" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Elfo", false)}>
                <img src={Elfo} alt="Elfo" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Luci", false)}>
                <img src={Luci} alt="Luci" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Mertz", false)}>
                <img src={Mertz} alt="Mertz" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Zog", false)}>
                <img src={Zog} alt="Zog" draggable={false} />
              </button>
            </div>
          )}

          {this.state.toggleShowCharChoice && (
            ///THIS IS TEMPORARY
            <div className="choose-next-character" style={{ left: "165px" }}>
              <button onClick={() => this.handleAddDialogBox("Bean", true)}>
                <img src={Bean} alt="Bean" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Elfo", true)}>
                <img src={Elfo} alt="Elfo" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Luci", true)}>
                <img src={Luci} alt="Luci" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Mertz", true)}>
                <img src={Mertz} alt="Mertz" draggable={false} />
              </button>
              <button onClick={() => this.handleAddDialogBox("Zog", true)}>
                <img src={Zog} alt="Zog" draggable={false} />
              </button>
            </div>
          )}
        </div>
      </div>
    );
  }
}

TopBar.propTypes = {
  addDialogBox: PropTypes.func.isRequired,
  howManyDialogBoxes: PropTypes.number.isRequired,
  dialogBoxInfo: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  howManyDialogBoxes: state.dialog.howManyDialogBoxes,
  dialogBoxInfo: state.dialog.dialogBoxInfo
});

export default connect(
  mapStateToProps,
  {
    addDialogBox
  }
)(TopBar);
