import React, { Component } from "react";
import PropTypes from "prop-types";

import { sortDialogSequence } from "../actions/dialogActions";
import { connect } from "react-redux";
import {
  Bean,
  Elfo,
  Luci,
  Mertz,
  Zog,
  PortraitPlaceholder
} from "./images/Images";

class DialogTester extends Component {
  constructor(props) {
    super(props);
    this.toggleDialogTestStart = this.toggleDialogTestStart.bind(this);
    this.renderDialogBoxes = this.renderDialogBoxes.bind(this);
    this.continueDialogSequence = this.continueDialogSequence.bind(this);
    this.backButton = this.backButton.bind(this);
    this.restartButton = this.restartButton.bind(this);
    this.choiceOnClick = this.choiceOnClick.bind(this);
    this.state = { dialogTestStart: false, showingDialog: 0 };
  }

  toggleDialogTestStart() {
    console.log(this.props.dialogBoxInfo);
    console.log(this.props.nodeConnections);
    console.log(this.props.howManyDialogBoxes);
    this.setState({
      dialogTestStart: !this.state.dialogTestStart,
      showingDialog: 0
    });
    this.props.sortDialogSequence(
      this.props.nodeConnections,
      this.props.dialogBoxInfo
    );
    this.renderDialogBoxes();
  }

  continueDialogSequence() {
    if (this.state.showingDialog + 1 < this.props.sortedDialogSequence.length) {
      if (
        this.props.sortedDialogSequence[this.state.showingDialog + 1].isChoice
      ) {
        this.setState({
          showingDialog: this.props.sortedDialogSequence.length - 1
        });
      } else {
        this.setState({ showingDialog: this.state.showingDialog + 1 });
      }
    }
  }

  renderDialogBoxes() {
    let dialogRender = [];
    //console.log("render this many" + this.state.showingDialog + 1);
    if (
      this.state.dialogTestStart &&
      this.props.sortedDialogSequence.length > 0
    ) {
      for (let i = 0; i <= this.state.showingDialog; i++) {
        if (this.props.sortedDialogSequence[i]) {
          const text = this.props.sortedDialogSequence[i].content;
          let charImage = PortraitPlaceholder;
          switch (this.props.sortedDialogSequence[i].image) {
            case "Bean":
              charImage = Bean;
              break;
            case "Elfo":
              charImage = Elfo;
              break;
            case "Luci":
              charImage = Luci;
              break;
            case "Mertz":
              charImage = Mertz;
              break;
            case "Zog":
              charImage = Zog;
              break;
            default:
              return;
          }

          const nameOfClass = this.props.sortedDialogSequence[i].isChoice
            ? "choice-test-render"
            : "dialog-test-render";

          const newRender = (
            <div
              className={nameOfClass}
              key={i}
              onClick={() =>
                this.choiceOnClick(
                  this.props.sortedDialogSequence[i].id,
                  this.props.sortedDialogSequence[i].isChoice
                )
              }
            >
              <img
                src={charImage}
                alt={this.props.sortedDialogSequence[i].image}
              />
              <h3>{this.props.sortedDialogSequence[i].image}</h3>
              <p>{text}</p>
            </div>
          );
          dialogRender.push(newRender);
        }
      }
    }
    return dialogRender;
  }

  restartButton() {
    this.setState({
      showingDialog: 0,
      dialogTestStart: !this.state.dialogTestStart
    });
  }

  backButton() {
    if (
      !this.props.sortedDialogSequence[this.state.showingDialog - 1].isChoice
    ) {
      this.setState({ showingDialog: this.state.showingDialog - 1 });
    }
  }

  choiceOnClick(id, isAChoice) {
    if (isAChoice) {
      this.props.sortDialogSequence(
        this.props.nodeConnections,
        this.props.dialogBoxInfo,
        id,
        this.props.sortedDialogSequence
      );
      let choiceStart = null;
      for (let i = 0; i < this.props.nodeConnections.length; i++) {
        if (this.props.nodeConnections[i][1] === id) {
          choiceStart = this.props.nodeConnections[i][0];
        }
      }
      let newSorted = this.props.sortedDialogSequence.filter(
        element => element[0] !== choiceStart || element[1] === id
      );
      this.setState({
        showingDialog: newSorted.length - 1
      });
    }
  }

  render() {
    let renderTheDialog = this.renderDialogBoxes();
    return (
      <div
        className="dialog-tester"
        onAnimationEnd={this.props.toggleHideEditor}
      >
        <div className="dialog-test-here">
          {!this.state.dialogTestStart && (
            <button
              className="start-dialog-button"
              onClick={this.toggleDialogTestStart}
            >
              START
            </button>
          )}
          {this.state.dialogTestStart && (
            <div
              className="continue-arrow"
              onClick={this.continueDialogSequence}
            >
              <img
                src="https://img.icons8.com/metro/26/000000/forward.png"
                alt="forward"
                draggable={false}
              />
            </div>
          )}
          {this.state.dialogTestStart && (
            <div
              className="dialog-render-area"
              onClick={this.continueDialogSequence}
            >
              {renderTheDialog}
            </div>
          )}
        </div>
        <div className="bottom-bar">
          <button className="go-back-button" onClick={this.backButton}>
            <img
              src="https://img.icons8.com/metro/26/000000/back.png"
              alt="back"
              draggable={false}
            />
          </button>
          <button className="restart-button" onClick={this.restartButton}>
            <img
              src="https://img.icons8.com/metro/26/000000/recurring-appointment.png"
              alt="restart"
              draggable={false}
            />
          </button>
        </div>
      </div>
    );
  }
}

DialogTester.propTypes = {
  sortDialogSequence: PropTypes.func.isRequired,
  dialogBoxInfo: PropTypes.array.isRequired,
  nodeConnections: PropTypes.array.isRequired,
  sortedDialogSequence: PropTypes.array.isRequired,
  howManyDialogBoxes: PropTypes.number.isRequired
};

const mapStateToProps = state => ({
  dialogBoxInfo: state.dialog.dialogBoxInfo,
  nodeConnections: state.dialog.nodeConnections,
  sortedDialogSequence: state.dialog.sortedDialogSequence,
  howManyDialogBoxes: state.dialog.howManyDialogBoxes
});

export default connect(
  mapStateToProps,
  { sortDialogSequence }
)(DialogTester);
