import React, { Component } from "react";
import DialogPiece from "./DialogPiece.js";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import {
  Bean,
  Elfo,
  Luci,
  Mertz,
  Zog,
  PortraitPlaceholder
} from "./images/Images";

class DialogEditor extends Component {
  constructor(props) {
    super(props);
    this.checkHowManyDialogsToShow = this.checkHowManyDialogsToShow.bind(this);
    this.renderNodeConnections = this.renderNodeConnections.bind(this);
  }

  checkHowManyDialogsToShow() {
    let dialogPieceCount = [];
    for (let i = 0; i < this.props.dialogBoxInfo.length; i++) {
      let charImage = PortraitPlaceholder;
      switch (this.props.dialogBoxInfo[i].image) {
        case "Bean":
          charImage = Bean;
          break;
        case "Elfo":
          charImage = Elfo;
          break;
        case "Luci":
          charImage = Luci;
          break;
        case "Mertz":
          charImage = Mertz;
          break;
        case "Zog":
          charImage = Zog;
          break;
        default:
          return;
      }
      const newDialogPiece = (
        <DialogPiece
          key={this.props.dialogBoxInfo[i].id}
          characterImage={charImage}
          character={this.props.dialogBoxInfo[i]}
        />
      );
      dialogPieceCount.push(newDialogPiece);
    }
    return dialogPieceCount;
  }

  renderNodeConnections() {
    let nodeRender = [];
    if (this.props.nodeConnections.length > 0) {
      for (let i = 0; i < this.props.nodeConnections.length; i++) {
        const startPoint = this.props.dialogBoxInfo[
          this.props.dialogBoxInfo.findIndex(
            x => x.id === this.props.nodeConnections[i][0]
          )
        ];
        const endPoint = this.props.dialogBoxInfo[
          this.props.dialogBoxInfo.findIndex(
            x => x.id === this.props.nodeConnections[i][1]
          )
        ];
        const newLine = (
          <svg
            width="100%"
            height="100%"
            key={i}
            style={{
              position: "absolute",
              pointerEvents: "none"
            }}
          >
            <line
              x1={startPoint.diagPosition[0] + 213}
              y1={startPoint.diagPosition[1] + 112}
              x2={endPoint.diagPosition[0] + 213}
              y2={endPoint.diagPosition[1] + 2}
              stroke="black"
            />
          </svg>
        );
        nodeRender.push(newLine);
      }
    }
    return nodeRender;
  }

  render() {
    let dialogPieceRender = this.checkHowManyDialogsToShow();
    let nodes = this.renderNodeConnections();
    return (
      <div className="dialog-editor">
        <div className="grid-background">
          {dialogPieceRender}
          {nodes}
        </div>
      </div>
    );
  }
}

DialogEditor.propTypes = {
  howManyDialogBoxes: PropTypes.number.isRequired,
  dialogBoxInfo: PropTypes.array.isRequired,
  stateChanged: PropTypes.number.isRequired,
  nodeConnections: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  howManyDialogBoxes: state.dialog.howManyDialogBoxes,
  dialogBoxInfo: state.dialog.dialogBoxInfo,
  stateChanged: state.dialog.stateChanged,
  nodeConnections: state.dialog.nodeConnections
});

export default connect(
  mapStateToProps,
  {}
)(DialogEditor);
