import {
  ADD_DIALOGBOX,
  REMOVE_DIALOGBOX,
  UPDATE_DIALOGBOX_TEXT,
  UPDATE_DROPPING_ID,
  UPDATE_DIAG_POSITION,
  UPDATE_STATE,
  CREATE_NODE_CONNECTION,
  SORT_DIALOG_SEQUENCE
} from "./types.js";

export const addDialogBox = (
  characterName,
  dBoxInfo,
  isChoice
) => async dispatch => {
  let newId = 0;
  let currentUsedId = [];
  for (let i = 0; i < dBoxInfo.length; i++) {
    currentUsedId.push(dBoxInfo[i].id);
  }
  while (currentUsedId.includes(newId)) {
    newId++;
  }
  let newDialogBox = {
    id: newId,
    image: characterName,
    isChoice: isChoice
  };

  dispatch({
    type: ADD_DIALOGBOX,
    payload: newDialogBox
  });
};

export const removeDialogBox = (
  dialogBoxObject,
  dialogBoxInfo
) => async dispatch => {
  let newdialogBoxInfo = dialogBoxInfo.filter(element => {
    return element.id !== dialogBoxObject.id;
  });

  dispatch({
    type: REMOVE_DIALOGBOX,
    payload: newdialogBoxInfo
  });
};
export const updateDialogBoxText = (
  text,
  dialogBoxInfo,
  id
) => async dispatch => {
  ///VERY POORLY OPTIMISED HERE

  let newDialogBoxInfo = dialogBoxInfo;
  for (let i = 0; i < dialogBoxInfo.length; i++) {
    if (id === dialogBoxInfo[i].id) {
      newDialogBoxInfo[i].content = text;
    }
  }
  dispatch({
    type: UPDATE_DIALOGBOX_TEXT,
    payload: newDialogBoxInfo
  });
};

export const updateDroppingId = id => async dispatch => {
  dispatch({
    type: UPDATE_DROPPING_ID,
    payload: id
  });
};

export const updateDiagPosition = (
  position,
  dialogBoxInfo,
  id
) => async dispatch => {
  const newDialogBoxInfo = dialogBoxInfo;

  newDialogBoxInfo[
    dialogBoxInfo.findIndex(x => x.id === id)
  ].diagPosition = position;
  dispatch({
    type: UPDATE_DIAG_POSITION,
    payload: newDialogBoxInfo
  });
};

export const updateState = reset => async dispatch => {
  let newPayload = reset;
  dispatch({
    type: UPDATE_STATE,
    payload: newPayload
  });
};

export const createNodeConnection = (
  startId,
  endId,
  nodeConnections,
  dialogBoxInfo
) => async dispatch => {
  let newNodeConnections = nodeConnections;
  let canAdd = true;
  //find if end connection is a choice
  const connectedToChoice = dialogBoxInfo.find(x => x.id === endId).isChoice;
  let cutHere = [];
  if (newNodeConnections.length > 0) {
    for (let i = 0; i < newNodeConnections.length; i++) {
      //remove connection if the connection already exists either way
      if (
        (newNodeConnections[i][0] === startId &&
          newNodeConnections[i][1] === endId) ||
        (newNodeConnections[i][1] === startId &&
          newNodeConnections[i][0] === endId)
      ) {
        canAdd = false;
        cutHere.push(i);
      }
      //remove other connections that this startID starts on any other connection
      if (
        newNodeConnections[i][0] === startId &&
        dialogBoxInfo.find(x => x.id === newNodeConnections[i][1]).isChoice ===
          false
      )
        cutHere.push(i);
      else if (
        newNodeConnections[i][0] === startId &&
        dialogBoxInfo.find(x => x.id === newNodeConnections[i][1]).isChoice ===
          true &&
        connectedToChoice === false
      )
        cutHere.push(i);
    }
  }
  cutHere.sort((a, b) => b - a);
  cutHere.forEach(x => newNodeConnections.splice(x, 1));
  if (canAdd) {
    newNodeConnections.push([startId, endId]);
  }
  //console.log(nodeConnections);
  dispatch({
    type: CREATE_NODE_CONNECTION,
    payload: newNodeConnections
  });
};

export const sortDialogSequence = (
  nodeConnections,
  dialogBoxInfo,
  startFromHere,
  previousSortedDialog
) => async dispatch => {
  let sortedIDSequence = [];
  let sortedDialogSequence = [];

  //if started from choice remove the other choices

  if (startFromHere !== undefined) {
    sortedDialogSequence = previousSortedDialog;
    let choiceStart = null;
    for (let i = 0; i < nodeConnections.length; i++) {
      if (nodeConnections[i][1] === startFromHere) {
        choiceStart = nodeConnections[i][0];
      }
    }
    for (let i = 0; i < nodeConnections.length; i++) {
      if (nodeConnections[i][0] === choiceStart) {
        sortedDialogSequence = sortedDialogSequence.filter(
          element => element.id !== nodeConnections[i][1]
        );
      }
    }
  }

  //find first node
  let foundNumbers = [];
  let notAvailableNumbers = [];
  let startNode = null;
  let endNode = [];
  for (let i = 0; i < nodeConnections.length; i++) {
    if (foundNumbers.includes(nodeConnections[i][0]))
      notAvailableNumbers.push(nodeConnections[i][0]);
    else {
      foundNumbers.push(nodeConnections[i][0]);
    }

    if (foundNumbers.includes(nodeConnections[i][1]))
      notAvailableNumbers.push(nodeConnections[i][1]);
    else {
      foundNumbers.push(nodeConnections[i][1]);
    }
  }

  ///CAN BE OPTIMISED
  const GetFirstNode = () => {
    let startAndEndNodes = foundNumbers.filter(
      x => !notAvailableNumbers.includes(x)
    );
    //console.log("start and end nodes" + startAndEndNodes);
    for (let i = 0; i < nodeConnections.length; i++) {
      if (
        startAndEndNodes.includes(nodeConnections[i][0]) &&
        !dialogBoxInfo[
          dialogBoxInfo.findIndex(x => x.id === nodeConnections[i][0])
        ].isChoice
      ) {
        startNode = nodeConnections[i][0];
      }
      if (
        startAndEndNodes.includes(nodeConnections[i][1]) &&
        !dialogBoxInfo[
          dialogBoxInfo.findIndex(x => x.id === nodeConnections[i][1])
        ].isChoice
      ) {
        endNode.push(nodeConnections[i][1]);
      }
    }
  };

  //console.log("start from here" + startFromHere);
  if (startFromHere !== undefined) {
    startNode = startFromHere;
  } else {
    GetFirstNode();
  }
  //console.log("start node " + startNode);
  //console.log("end nod" + endNode);
  //fill in the rest of the node sequence

  let numberLookingFor = startNode;
  let choices = [];
  let canContinue = true;

  sortedIDSequence.push(startNode);
  sortedDialogSequence.push(
    dialogBoxInfo[dialogBoxInfo.findIndex(x => x.id === startNode)]
  );

  const AddNodeToSort = () => {
    nodeConnections.forEach(element => {
      if (element[0] === numberLookingFor && canContinue) {
        //number looking for updated
        numberLookingFor = element[1];
        //add second dialog box
        sortedIDSequence.push(element[1]);
        sortedDialogSequence.push(
          dialogBoxInfo[dialogBoxInfo.findIndex(x => x.id === element[1])]
        );
        //if second dialog box is a choice then push the first dialog box id for later
        if (
          dialogBoxInfo[dialogBoxInfo.findIndex(x => x.id === element[1])]
            .isChoice
        ) {
          choices.push(
            dialogBoxInfo[dialogBoxInfo.findIndex(x => x.id === element[0])].id
          );
          canContinue = false;
        }
      }
    });
  };

  const GetAllChoices = choiceID => {
    nodeConnections.forEach(element => {
      if (element[0] === choiceID && !sortedIDSequence.includes(element[1])) {
        sortedIDSequence.push(element[1]);
        sortedDialogSequence.push(
          dialogBoxInfo[dialogBoxInfo.findIndex(x => x.id === element[1])]
        );
      }
    });
  };

  for (let i = 0; i < 100; i++) {
    if (
      endNode.includes(numberLookingFor) ||
      choices.length !== 0 ||
      !canContinue
    ) {
      AddNodeToSort();
      if (choices.length !== 0) {
        GetAllChoices(choices[0]);
      }
      break;
    }
    canContinue = true;
    AddNodeToSort();
  }

  if (sortedDialogSequence[0] === undefined) {
    sortedDialogSequence = [];
  }

  //console.log("end sorted id" + sortedIDSequence);
  //console.log("end sorted dialog" + sortedDialogSequence);
  //console.log("end choices" + choices);

  dispatch({
    type: SORT_DIALOG_SEQUENCE,
    payload: sortedDialogSequence
  });
};
