import React, { Component } from "react";
import Draggable from "react-draggable";
import {
  removeDialogBox,
  updateDialogBoxText,
  updateDroppingId,
  updateDiagPosition,
  updateState,
  createNodeConnection
} from "../actions/dialogActions";
import { connect } from "react-redux";
import PropTypes from "prop-types";

class DialogPiece extends Component {
  constructor(props) {
    super(props);
    this.handleUpdateText = this.handleUpdateText.bind(this);
    this.handleNodeDrag = this.handleNodeDrag.bind(this);
    this.handleDiagDrag = this.handleDiagDrag.bind(this);
    this.handleDiagStop = this.handleDiagStop.bind(this);
    this.handleStop = this.handleStop.bind(this);
    this.handleDropEnter = this.handleDropEnter.bind(this);
    this.handleDropLeave = this.handleDropLeave.bind(this);
    this.componentDidMount = this.componentDidMount.bind(this);
    this.checkWhichColorNode = this.checkWhichColorNode.bind(this);

    this.state = {
      nodeDragging: false,
      pointerEvent: "auto",
      deltaPosition: this.props.character.diagPosition,
      startPosition: {
        x: this.props.character.diagPosition[0],
        y: this.props.character.diagPosition[1]
      }
    };
  }

  componentDidMount() {
    this.setState({
      startPosition: null
    });
  }

  handleUpdateText(e) {
    this.props.updateDialogBoxText(
      e.target.value,
      this.props.dialogBoxInfo,
      this.props.character.id
    );
  }

  handleNodeDrag() {
    this.setState({ nodeDragging: true, pointerEvent: "none" });
  }

  handleDiagDrag(e, ui) {
    const newPos = [
      this.state.deltaPosition[0] + ui.deltaX,
      this.state.deltaPosition[1] + ui.deltaY
    ];
    this.props.updateState(1);
    this.setState({ deltaPosition: newPos });
    this.props.updateDiagPosition(
      this.state.deltaPosition,
      this.props.dialogBoxInfo,
      this.props.character.id
    );
  }

  handleDiagStop() {
    this.props.updateDiagPosition(
      this.state.deltaPosition,
      this.props.dialogBoxInfo,
      this.props.character.id
    );
    this.props.updateState("reset");
  }

  handleStop() {
    this.setState({ nodeDragging: false, pointerEvent: "auto" });
    //console.log(`will drop on ${this.props.droppingId}`);
    if (this.props.droppingId !== null) {
      this.props.createNodeConnection(
        this.props.character.id,
        this.props.droppingId,
        this.props.nodeConnections,
        this.props.dialogBoxInfo
      );
    }
    this.props.updateState(1);
  }

  handleDropEnter() {
    if (this.state.nodeDragging === false) {
      this.props.updateDroppingId(this.props.character.id);
    }
  }
  handleDropLeave() {
    this.props.updateDroppingId(null);
  }

  checkWhichColorNode() {
    let newColor = "darkgray";
    if (this.props.nodeConnections.length > 0) {
      this.props.nodeConnections.forEach(x => {
        if (x[1] === this.props.character.id) {
          newColor = "#7dcdd0";
        }
      });
    }
    const newTop = "-3px";
    const newStyle = {
      top: newTop,
      background: newColor,
      border: `2px solid ${this.borderColor()[0]}`
    };
    return newStyle;
  }

  borderColor() {
    const choiceColors = ["#1b5f50", "#42bd92", "#bdddc9"];
    const diagColors = ["#54302c", "#b87953", "#d9cabd"];
    if (this.props.character.isChoice) {
      return choiceColors;
    }

    return diagColors;
  }

  render() {
    return (
      <Draggable
        axis="both"
        handle=".dialog-character-pic"
        defaultPosition={{ x: 0, y: 0 }}
        position={this.state.startPosition}
        bounds=".dialog-editor"
        scale={1}
        onDrag={this.handleDiagDrag}
        onStop={this.handleDiagStop}
      >
        <div
          className="dialog-piece"
          onMouseEnter={this.handleDropEnter}
          onMouseLeave={this.handleDropLeave}
          style={{
            pointerEvents: this.state.pointerEvent,
            boxShadow: `inset 0 0 2px 1px ${
              this.borderColor()[0]
            }, inset 0 0 2px 4px ${this.borderColor()[1]}, inset 0 0 1px 5px ${
              this.borderColor()[2]
            } `,
            border: `1px solid ${this.borderColor()[0]}`,
            background: this.borderColor()[0]
          }}
        >
          <button
            className="delete-button"
            onClick={() =>
              this.props.removeDialogBox(
                this.props.character,
                this.props.dialogBoxInfo
              )
            }
          >
            <span>&#10006;</span>
          </button>
          <div className="dialog-character-pic">
            <img
              alt="profile pic"
              src={this.props.characterImage}
              draggable={false}
              style={{
                border: `1px solid ${this.borderColor()[0]}`
              }}
            />
          </div>
          <div className="dialog-content">
            <textarea
              placeholder={`Hi I am ${this.props.character.image}`}
              onChange={this.handleUpdateText}
              style={{
                border: `1px solid ${this.borderColor()[0]}`
              }}
              defaultValue={this.props.character.content}
            ></textarea>
          </div>
          <div className="node-socket" style={this.checkWhichColorNode()} />
          <div
            className="node-socket"
            style={{
              bottom: "-3px",
              border: `2px solid ${this.borderColor()[0]}`
            }}
          />

          <Draggable
            axis="both"
            handle=".node-handle"
            position={{ x: 0, y: 0 }}
            scale={1}
            onDrag={this.handleNodeDrag}
            onStop={this.handleStop}
          >
            <div
              className="node-handle"
              id="node-handle"
              style={{
                cursor: "pointer",
                border: `2px solid ${this.borderColor()[0]}`
              }}
            />
          </Draggable>
        </div>
      </Draggable>
    );
  }
}

DialogPiece.propTyes = {
  removeDialogBox: PropTypes.func.isRequired,
  updateDialogBoxText: PropTypes.func.isRequired,
  updateDroppingId: PropTypes.func.isRequired,
  updateDiagPosition: PropTypes.func.isRequired,
  updateState: PropTypes.func.isRequired,
  createNodeConnection: PropTypes.func.isRequired,
  dialogBoxInfo: PropTypes.array.isRequired,
  droppingId: PropTypes.number.isRequired,
  nodeConnections: PropTypes.array.isRequired,
  stateChanged: PropTypes.number.isRequired
};

const mapStateToProps = state => ({
  dialogBoxInfo: state.dialog.dialogBoxInfo,
  droppingId: state.dialog.droppingId,
  nodeConnections: state.dialog.nodeConnections,
  stateChanged: state.dialog.stateChanged
});

export default connect(
  mapStateToProps,
  {
    removeDialogBox,
    updateDialogBoxText,
    updateDroppingId,
    updateDiagPosition,
    updateState,
    createNodeConnection
  }
)(DialogPiece);
